<template>
    <div id="config-index">
        <public-header title="设置管理"></public-header>
        <div class="content">
            <template v-if="util.checkperm('config.mall')">
                <div class="tip">平台</div>
                <van-cell-group>
                    <van-cell title="基础设置" is-link :to="util.getUrl({path: '/pages/config/mall/basic'})" />
                    <van-cell title="平台状态" is-link :to="util.getUrl({path: '/pages/config/mall/status'})"/>
                </van-cell-group>
            </template>
            <template v-if="util.checkperm('config.takeout')">
                <div class="tip">订单相关</div>
                <van-cell-group>
                    <van-cell title="配送员/店员通知规则" is-link :to="util.getUrl({path: '/pages/config/takeout/notify'})"/>
                    <van-cell title="时限要求" is-link :to="util.getUrl({path: '/pages/config/takeout/timelimit'})"/>
                    <van-cell title="订单设置" is-link :to="util.getUrl({path: '/pages/config/takeout/order'})"/>
                    <van-cell title="配送/订单调度设置" is-link :to="util.getUrl({path: '/pages/config/takeout/delivery'})"/>
                    <van-cell title="积分赠送" is-link :to="util.getUrl({path: '/pages/config/takeout/grantnum'})"/>
                    <van-cell title="订单提交" is-link :to="util.getUrl({path: '/pages/config/takeout/ordersubmit'})"/>
                </van-cell-group>
            </template>
        </div>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader
		},
		methods: {

		}
	}
</script>

<style>
    #config-index .content .tip{
        padding: 10px 15px;
        font-size: 14px;
        color: #7a7a7a;
    }
</style>


